/* eslint react/no-danger: "off" */

import React from 'react'
import co from 'co'
import MaxwinClient from 'maxwin_client'
import Future from 'promise-future'
import Big from 'big.js'
import './app.sass'
import {qrCodeSvg, formatTime, sleep} from '../utils'

class AppComponent extends React.Component {

  componentDidMount() {
    this.api = new MaxwinClient.GamesApi()
    this.actions = this.props.actions
    co(this.loadCurrentGame())
  }

  getPaymentUri() {
    const {currentGame} = this.props
    return `bitcoin:${currentGame.address}?amount=${this.calcNextBet()}&label=Iceberg`
  }

  * getCurrentGame() {
    const games = yield Future.call(this.api.getCurrentGames.bind(this.api))
    return games[0]
  }

  calcNextBet() {
    const {currentGame} = this.props
    let nextBet
    const maxBet = new Big(currentGame.maxAmount || 0)
    if (maxBet.gt(0)) {
      nextBet = maxBet.plus(new Big('0.00000001')).toString()
    } else {
      nextBet = new Big('0.002').toString()
    }
    return nextBet
  }

  * loadCurrentGame() {
    yield [
      this.updateGame(),
      this.updateTime(),
    ]
  }

  * updateGame() {
    this.actions.updateCurrentGame(yield this.getCurrentGame())
    const code = yield qrCodeSvg(this.getPaymentUri())
    this.actions.updateQrCode(code)

    yield sleep(3000)
    yield this.updateGame()
  }

  * updateTime() {
    this.actions.updateTime()

    yield sleep(500)
    yield this.updateTime()
  }

  render() {
    const {currentGame} = this.props
    return currentGame.maxAmount === undefined ?
      <div/> : (
        <div>
          <header>
            <section>
              <div>&nbsp;</div>
              <div>Iceberg<span>first fair bitcoin lottery</span></div>
              <div>&nbsp;</div>
            </section>
          </header>
          <section>
            <div>&nbsp;</div>
            <div className="index">
              <div>
                <div>Current game: <a
                  href={`https://blockchain.info/address/${currentGame.address}`}>{currentGame.address}</a></div>
              </div>
              <div className="qrBar">
                <div className="qrCode" dangerouslySetInnerHTML={{__html: currentGame.payCode}}/>
                <div className="qrInfo">
                  <div><a href={this.getPaymentUri()}>Pay {this.calcNextBet()} BTC</a></div>
                  <div>to win</div>
                  <div>{currentGame.balance} BTC</div>
                  <div>{formatTime(currentGame.timeLeft)}</div>
                </div>
              </div>
            </div>
            <div>&nbsp;</div>
          </section>
        </div>
    )
  }
}

AppComponent.propTypes = {
  actions: React.PropTypes.shape({
    updateCurrentGame: React.PropTypes.func.isRequired,
    updateTime: React.PropTypes.func.isRequired,
    updateQrCode: React.PropTypes.func.isRequired,
  }).isRequired,
  currentGame: React.PropTypes.shape({
    address: React.PropTypes.string,
    payCode: React.PropTypes.string,
    timeLeft: React.PropTypes.number,
    balance: React.PropTypes.string,
  }).isRequired,
}

AppComponent.defaultProps = {
}

export default AppComponent



